<template>
  <el-form label-position="right"
           :label-width="type==='add' ? '98px' : '82px'"
           width='100%'
           size="mini"
           :cell-style="{marginBottom:'10px'}"
           :model="formValue">
    <el-form-item>
      <span slot='label'>
        <el-checkbox class="checkBox"
                     v-model="checked.specimenNumber"
                     v-if="type==='add'">&nbsp;</el-checkbox>
        标本号:
      </span>
      <el-input v-model="formValue.specimenNumber"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item>
      <span slot='label'>
        <el-checkbox class="checkBox"
                     v-model="checked.applicationStatus"
                     v-if="type==='add'"> </el-checkbox>
        申请状态:
      </span>
      <el-select v-model="formValue.applicationStatus"
                 placeholder="请选择">
        <el-option label="未申请"
                   value="未申请" />

        <el-option label="已申请"
                   value="已申请" />

      </el-select>
    </el-form-item>
    <el-form-item label="姓名:">
      <el-input v-model="formValue.name"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="性别:">
      <el-select v-model="formValue.sex"
                 placeholder="请选择">
        <el-option label="男"
                   value="男" />
        <el-option label="女"
                   value="女" />
      </el-select>
    </el-form-item>
    <el-form-item label="年龄:">
      <el-input v-model="formValue.age"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="病历号:">
      <el-input v-model="formValue.caseNumber"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="床号:">
      <el-input v-model="formValue.bedNo"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="送检科室:">
      <el-input v-model="formValue.inspectionDepartment"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="送检医师:">
      <el-input v-model="formValue.inspectionDoctor"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="就诊类型:">
      <el-select v-model="formValue.diagnosisType"
                 placeholder="请选择">
      </el-select>
    </el-form-item>
    <el-form-item label="标本状态:">
      <el-select v-model="formValue.specimenStatus"
                 placeholder="请选择">
      </el-select>
    </el-form-item>
    <el-form-item label="送检标本:">
      <el-input v-model="formValue.inspectionSpecimen"
                placeholder='请输入'></el-input>
    </el-form-item>

    <el-form-item label="检验时间:">
      <el-date-picker v-model="formValue.inspectionTime"
                      type="datetime"
                      placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="审核时间:">
      <el-date-picker v-model="formValue.reviewTime"
                      type="datetime"
                      placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="打印时间:">
      <el-date-picker v-model="formValue.printTime"
                      type="datetime"
                      placeholder="选择日期时间">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="临床诊断:">
      <el-input v-model="formValue.clinicalDiagnosis"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="详细年龄:">
      <el-input v-model="formValue.detailedAge"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="备注:">
      <el-input v-model="formValue.remark"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="申请时间:">
      <el-input v-model="formValue.applicationTime"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="打印状态:">
      <el-input v-model="formValue.printStatus"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="收费项目:">
      <el-input v-model="formValue.chargeItems"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="检验者:">
      <el-input v-model="formValue.examiner"
                placeholder='请输入'></el-input>
    </el-form-item>
    <el-form-item label="审核者:">
      <el-input v-model="formValue.inspector"
                placeholder='请输入'></el-input>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'FormList',
  components: {},
  props: ['type','checked'],

  data() {
    return {
      formValue: {
        specimenNumber: '',
        applicationStatus: '',
        name: '',
        sex: '',
        age: '',
        caseNumber: '', // 病例号
        bedNo: '', // 床号
        inspectionDepartment: '', // 送检科室
        inspectionDoctor: '', // 送检医师
        diagnosisType: '', // 就诊类型
        specimenStatus: '', // 标本状态
        inspectionSpecimen: '', // 送检标本
        inspectionTime: '', // 检验时间
        reviewTime: '', // 审核时间
        printTime: '', // 打印时间
        clinicalDiagnosis: '', // 临床诊断
        detailedAge: '', // 详细年龄
        remark: '', // 备注
        applicationTime: '', // 申请时间
        printStatus: '', // 打印状态
        chargeItems: '', // 收费项目
        examiner: '', // 检验者
        inspector: '', // 审核者
        // printTime: '', // 打印时间
      },
    }
  },
}
</script>
<style scoped>
/* 表单列表之间的间距 */
.el-form-item--mini.el-form-item {
  margin-bottom: 10px;
}
/* 解决表单列表 input、select ... 框参差不齐的样式*/
.el-form-item {
  margin-right: 0 !important;
}
.el-form-item__label {
  position: absolute;
}
.el-form-item__conntent {
  width: 100%;
  padding-left: 80px;
}
.el-select,
.el-input_inner,
.el-date-editor--datetime {
  width: 100%;
}
</style>
