<template>
  <el-drawer title="新增"
             size="45%"
             :visible="isVisibleDrawer"
             direction="rtl"
             :wrapperClosable='false'
             :destroy-on-close='true'
             @close="$emit('closeDrawer')">
    <el-row class="addContentWarp">
      <el-col :span="10">
        <el-checkbox v-model="checked.selectedL"
                     :style="{marginTop:'5px'}"
                     class="check">勾选字段将保留,方便下次录入</el-checkbox>
        <FormList type='add'
                  :checked='checked' />
      </el-col>
      <el-col :span="14">
        <div class="searchWarp">
          <el-input placeholder="请输入内容"
                    prefix-icon="el-icon-search"
                    size="small"
                    v-model="searchInfo">
          </el-input>
          <el-checkbox v-model="checked.selectedR"
                       :style="{marginTop:'5px'}"
                       class="checked.selectedR">勾选字段将保留,方便下次录入</el-checkbox>
        </div>

        <el-table :data="tableData"
                  :header-cell-style="{textAlign: 'center'}"
                  :cell-style="{textAlign:'center'}"
                  border>
          <el-table-column type="index"
                           label="No."
                           width="50px">
          </el-table-column>
          <el-table-column type="selection"
                           width="55">
          </el-table-column>
          <el-table-column prop="chargeItem"
                           label="收费项目">
          </el-table-column>
          <el-table-column prop="charge"
                           label="收费">
          </el-table-column>
          <el-table-column prop="items"
                           label="项目">
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24"
              class="saveRow">
        <el-button size="small"
                   @click="saveClick"
                   type="primary">保&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;存</el-button>
      </el-col>
    </el-row>
  </el-drawer>

</template>

<script>
import FormList from '@/components/home/FormList.vue'

export default {
  name: 'Frame',
  components: {
    FormList,
  },
  props: ['isVisibleDrawer'],
  data() {
    return {
      formValue: {
        specimenNumber: '',
        applicationStatus: '',
        name: '',
        sex: '',
        age: '',
        caseNumber: '', // 病例号
        bedNo: '', // 床号
        inspectionDepartment: '', // 送检科室
        inspectionDoctor: '', // 送检医师
        diagnosisType: '', // 就诊类型
        specimenStatus: '', // 标本状态
        inspectionSpecimen: '', // 送检标本
        inspectionTime: '', // 检验时间
        reviewTime: '', // 审核时间
        printTime: '', // 打印时间
        clinicalDiagnosis: '', // 临床诊断
        detailedAge: '', // 详细年龄
        remark: '', // 备注
        applicationTime: '', // 申请时间
        printStatus: '', // 打印状态
        chargeItems: '', // 收费项目
        examiner: '', // 检验者
        inspector: '', // 审核者
      },
      searchInfo: '',
      checked: {
        selectedL: true,
        selectedR: true,
        specimenNumber: true,
        applicationStatus: false,
      },
      tableData: [
        {
          chargeItem: '掉料',
          charge: '项目',
          items: '第一项目',
        },
      ],
    }
  },
  methods: {
    saveClick() {
      this.$emit('addSaveClick')
    },
  },
}
</script>

<style scoped>
::v-deep .el-drawer.rtl {
  background-color: #f0f0f0;
}
::v-deep .el-drawer__header {
  margin: 0;
}
.addContentWarp {
  padding-top: 20px;
  height: 100%;
}
.addContentWarp > div {
  padding: 0 5px;
}
.addContentWarp .check {
  margin-left: 8px;
  margin-bottom: 20px;
}
.addContentWarp .searchWarp {
  display: flex;
  margin-bottom: 13px;
}
.addContentWarp .searchWarp > div {
  margin-right: 16px;
}
.addContentWarp .saveRow {
  text-align: right;
}
</style>